.login {
  padding: 40px 0;
  display: flex;
  justify-content: center;

  .container {
    width: 300px;
    height: 400px;
    display: flex;
    flex-direction: column;
    border: 1px solid #c9c9c9;
    border-radius: 7px;
    justify-content: center;
    align-items: center;
    padding: 30px;

    & input {
      padding: 10px;
      margin: 5px;
      height: 15px;
      font-size: 15px;
    }
  }

  & button {
    width: 40%;
  }
}

